
import { Component, Vue, Prop } from 'vue-property-decorator';
import VueTippy, { TippyComponent } from 'vue-tippy';

Vue.use(VueTippy);

@Component({ components: { TippyComponent } })
export default class I extends Vue {
    // dark or bright
    @Prop({ type: Boolean, default: false }) dark!: boolean;
    @Prop({ type: String, default: 'test test test' }) text!: string;

    tippySettings = {
        placement: 'top',
        arrow: true,
        theme: this.dark ? 'light' : 'dark', // tip is inverted
        delay: [0, 100],
    }
}
