
import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

@Component({ components: {}, model: { prop: 'on', event: 'change' } })
export default class FlipSwitch extends Vue {
    @Prop({ type: String, default: Math.random().toString(16) }) id!: string;
    @Prop({ type: String, default: '' }) label!: string;
    @Prop({ type: String, default: 'green' }) color!: string;
    @Prop(Boolean) on: boolean = false;

    ison = false;

    @Watch('on')
    onChange(newValue) {
        this.ison = newValue;
    }
}
