
import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import { AmountInput } from '@nimiq/vue-components';
import ConfettiGenerator from 'confetti-js';
import FlipSwitch from './FlipSwitch.vue';
import I from './I.vue';
import { isPrestakingEmbedLayout } from '../lib/layout';

export type Setting = {
    staking: number,
    period: number,
    ratio: number,
    restake: boolean,
    fee: number,
    rate: number,
    fork: string,
}

const now = new Date();
const twoDigits = (n: number) => `${n < 10 ? '0' : ''}${n}`;

export const defaultSettings: Setting = {
    staking: 100000,
    period: isPrestakingEmbedLayout ? 1 : 2,
    ratio: 0.5,
    restake: isPrestakingEmbedLayout,
    fee: 0,
    rate: 0,
    fork: isPrestakingEmbedLayout
        ? '2024-11-19'
        : `${now.getFullYear()}-${twoDigits(now.getMonth() + 1)}-${twoDigits(now.getDate())}`,
};

@Component({ components: { AmountInput, FlipSwitch, I } })
export default class Settings extends Vue {
    @Prop(Number) usdRate!: number;
    private advanced: boolean = false;
    private poolFeelEnabled = false;
    private poolFee = 10;
    private cg: ConfettiGenerator;
    private isPrestakingEmbedLayout = isPrestakingEmbedLayout;

    settings = defaultSettings;

    async created() {
        try {
            // Note that CoinGecko is increasingly limiting access without API key. For example, wallet.nimiq.com has
            // already been blocked from unauthenticated access, thus the Wallet has been switched to CryptoCompare as
            // fiat api provider. Should such limitations become noticeable in the Staking Calculator too, it can be
            // switched to CryptoCompare as well, either as a manual fetch, or by using the FiatApi from @nimiq/utils.
            // Note that due to some modern js syntax, @nimiq/utils will have to be added to transpileDependencies in
            // vue.config.js.
            const response = await fetch('https://api.coingecko.com/api/v3/simple/price?ids=nimiq-2&vs_currencies=usd');
            const rate = `${(await response.json())['nimiq-2'].usd}`.slice(0, 8);
            console.log('Calculator NIM rate', rate);
            this.settings.rate = parseFloat(rate);
        } catch (error) {
            console.warn(error);
            this.settings.rate = 0.001;
        }
    }

    // can be refactored into a reusable radio button component
    handleButtons(event: MouseEvent, defaultValue: string): String {
        const button = event.srcElement as HTMLButtonElement;
        if (!button.value) return defaultValue; // when clicking between buttons, don't change anything
        this.settingsChanged.bind(this); // just to use this :(
        button.parentElement!.querySelectorAll('button').forEach(
            other => other.classList.toggle('active', other.value === button.value),
        );
        return button.value;
    }

    get stakingUsd() {
        return this.usdRate ? `~$${(this.settings.staking * this.usdRate).toFixed(0)}` : '?';
    }

    onAmountInputBlur() {
        this.settings.staking = Math.min(100000000, Math.max(1000, this.settings.staking));
    }

    @Watch('poolFee')
    poolFeeChanged(value: number) {
        this.settings.fee = value / 100;
    }

    @Watch('settings', { immediate: true, deep: true })
    settingsChanged(settings: Setting) {
        settings.rate = Math.min(settings.rate, 10);
        if (!this.cg && settings.rate >= 1) {
            this.cg = new ConfettiGenerator({
                target: 'bg-canvas',
                max: 500,
                start_from_edge: true,
                respawn: false,
                clock: 66,
            });
            this.cg.render();
        }
        // clone settings to not pass a reference
        this.$emit('change', JSON.parse(JSON.stringify(settings)));
    }
}
