
import { Component, Vue, Prop } from 'vue-property-decorator';
import Chart, { ChartPoint } from 'chart.js';
import { formatNumber, formatDate } from '../lib/utils';
import I from './I.vue';
import { isPrestakingEmbedLayout } from '../lib/layout';

const GRAPH_POINTS = 52;
const YEAR = 365.25 * 24 * 60 * 60 * 1000;

export type GraphEntry = {
    enabled: boolean,
    r: number,
    g: number,
    b: number,
    function: Function,
    label?: string,
}

const rgba = (r: number, g: number, b: number, a: number) => `rgba(${r}, ${g}, ${b}, ${a})`;

@Component({ components: { I } })
export default class Graph extends Vue {
    @Prop({ type: Number, required: true }) staking!: number;
    @Prop({ type: Array, required: true }) graphs!: GraphEntry[];
    @Prop({ type: Number, default: 1 }) period!: number;
    @Prop({ type: Number }) minPeriod!: number;
    @Prop({ type: Number }) maxPeriod!: number;
    @Prop({ type: Number, default: 0 }) usdRate?: number;

    private readonly extendRange = 1.1;
    private chart?: any;
    private start = Date.now();
    private sliderPosition?: number;
    private valueAtSlider: number = 0;
    private format = formatNumber;
    private isPrestakingEmbedLayout = isPrestakingEmbedLayout;

    createChart() {
        const canvas = this.$refs.canvas as HTMLCanvasElement;
        const ctx = canvas.getContext('2d')!;

        Chart.defaults.global.defaultFontColor = 'rgba(31, 35, 72, 0.4)';
        Chart.defaults.global.defaultFontFamily = 'Muli, system-ui, sans-serif';
        Chart.defaults.global.defaultFontSize = 12;
        Chart.defaults.global.defaultFontStyle = 'bold';

        this.chart = new Chart(ctx, {
            type: 'line',
            options: {
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                    yAxes: [{
                        stacked: false,
                        gridLines: {
                            display: true,
                            color: 'rgba(31, 35, 72, .1)', // nimiq-blue with low opacity
                            drawBorder: false,
                        },
                        ticks: {
                            min: 0,
                            padding: 8,
                            callback: (value) => {
                                const total = typeof value === 'string'
                                    ? parseFloat(value) + this.staking
                                    : value + this.staking;
                                return total >= 100000
                                    ? `${formatNumber(Math.floor(total / 1000))} kNIM`
                                    : `${formatNumber(Math.floor(total))} NIM`;
                            },
                        },
                    }],
                    xAxes: [{
                        type: 'time',
                        gridLines: {
                            display: true,
                            drawOnChartArea: false,
                            drawTicks: true,
                            lineWidth: 2,
                        },
                        time: {
                            unit: 'month',
                        },
                        ticks: {
                            padding: 8,
                            maxRotation: 0,
                            maxTicksLimit: 4,
                            callback(value, index) {
                                const months = index;
                                return formatDate(months);
                            },
                        },
                    }],
                },
                tooltips: {
                    enabled: false,
                },
                legend: {
                    display: false,
                },
            },
            data: {
                datasets: this.datasets,
            },
        });
        console.log('Graph.create', this.chart);
    }

    async updateChart(_sliderPosition?: number) {
        const { datasets } = this;

        const canvas = this.$refs.canvas as HTMLCanvasElement;
        if (!canvas) return; // happens during initialization when welcome screen is shown
        const slider = this.$refs.slider as HTMLDivElement;
        const text = this.$refs.text as HTMLElement;
        const sliderPosition = _sliderPosition || this.calculateInitialPosition;
        const dot = this.$refs.dot as HTMLElement;
        const date = this.$refs.date as HTMLElement;
        this.sliderPosition = sliderPosition;
        const ctx = canvas.getContext('2d')!;
        const chart = this.chart!;
        const xAxis = chart.scales['x-axis-0'];
        const yAxis = chart.scales['y-axis-0'];
        const graphFading = window.innerWidth > window.innerHeight ? 30 : 15;
        const constrainedSliderPosition = Math.max(xAxis.left, Math.min(xAxis.right - graphFading, sliderPosition));
        const sliderFlipped = window.innerWidth > window.innerHeight
            ? constrainedSliderPosition < xAxis.left + text.offsetWidth // landscape mode, enough space
            : constrainedSliderPosition + text.offsetWidth < canvas.offsetWidth; // accept overlapping y-axis

        // Update slider
        slider.style.bottom = `${xAxis.height}px`;
        slider.style.left = `${constrainedSliderPosition - 3}px`;
        slider.classList.toggle('flipped', sliderFlipped);

        // scale Y to 33% of max Y for lowest period and 100% for longest period + leave 20% for the info box
        const periodRatio = (this.period - this.minPeriod) / (this.maxPeriod - this.minPeriod);
        const maxY = this.currentMaxY * (3.2 - 2 * (periodRatio ** (1 / 3)));
        chart.options.scales.yAxes[0].ticks.max = maxY;

        // make step scale show more ticks with longer range
        chart.options.scales.yAxes[0].ticks.stepSize = maxY / (5 + 2.5 * periodRatio);

        chart.update(); // because following functions depend on graph data to be updated

        const sliderTime = xAxis.getValueForPixel(constrainedSliderPosition);
        const highest = this.graphs.map(graph => ({
            value: graph.enabled ? graph.function(this.start, sliderTime) : 0,
            graph,
        })).sort((a, b) => b.value - a.value)[0];

        this.graphs.forEach(async (graph, index) => {
            const dataset = datasets[index];
            const { r, g, b } = graph;

            if (!graph.enabled) {
                // hide disabled graphs
                dataset.borderColor = 'transparent';
                dataset.backgroundColor = 'transparent';
                return;
            }

            // fade graph
            const relativeOffset = Math.max(0, Math.min(1, constrainedSliderPosition / canvas.width));
            const lineGradient = ctx.createLinearGradient(0, 0, canvas.width, 0);
            lineGradient.addColorStop(relativeOffset, rgba(r, g, b, 1));
            lineGradient.addColorStop(
                Math.max(0, Math.min(1, (constrainedSliderPosition + graphFading) / canvas.width)),
                rgba(r, g, b, 0),
            );
            dataset.borderColor = lineGradient;

            // cut graph fill
            if (graph === highest.graph) {
                // full color fill
                // const fillGradient = ctx.createLinearGradient(0, 0, canvas.width, 0);
                // fillGradient.addColorStop(relativeOffset, rgba(r, g, b, 0.1));
                // fillGradient.addColorStop(relativeOffset, rgba(r, g, b, 0));
                // dataset.backgroundColor = fillGradient;

                // fill colored line pattern
                const patternCanvas = document.createElement('canvas');
                const patternContext = patternCanvas.getContext('2d')!;
                patternCanvas.width = canvas.width; // constrainedSliderPosition;
                patternCanvas.height = canvas.height;

                patternContext.lineWidth = 1;
                patternContext.strokeStyle = rgba(r, g, b, 0.8);
                for (let x = 0; x <= constrainedSliderPosition; x += 8) {
                    patternContext.moveTo(x, 0);
                    patternContext.lineTo(x, canvas.height);
                }
                patternContext.stroke();

                let alpha = 0.2;
                for (let x = constrainedSliderPosition - (constrainedSliderPosition % 8); x <= canvas.width; x += 8) {
                    patternContext.strokeStyle = rgba(31, 35, 72, alpha);
                    patternContext.beginPath();
                    patternContext.moveTo(x, 0);
                    patternContext.lineTo(x, canvas.height);
                    patternContext.stroke();
                    alpha *= 0.8;
                }

                const pattern = ctx.createPattern(patternCanvas, 'no-repeat');
                dataset.backgroundColor = pattern!;
            } else {
                dataset.backgroundColor = 'transparent';
            }
        });

        // trigger update of tooltip and place dot
        await this.$nextTick();
        const time = xAxis.getValueForPixel(constrainedSliderPosition);
        const { value } = highest;
        this.valueAtSlider = value;

        await this.$nextTick();
        dot.style.top = `${yAxis.getPixelForValue(value)}px`;
        const totalYears = (new Date(time).getTime() - this.start) / YEAR;
        const months = Math.floor(totalYears * 12); // 1.6 > 19.2 > 19; 1.6 - 1.583333333 = 0.016666666
        const days = Math.floor((totalYears - months / 12) * 365.25);
        date.innerText = formatDate(months, days);

        chart.update();
    }

    update(resetSlider = false) {
        this.updateChart(resetSlider ? undefined : this.sliderPosition);
    }

    mounted() {
        const canvas = this.$refs.canvas as HTMLCanvasElement;
        const container = this.$refs.container as HTMLDivElement;
        const offset = container.offsetLeft;

        this.createChart();

        canvas.addEventListener('click', e => this.updateChart(e.offsetX));
        container.addEventListener('mousemove', (e) => { if (e.buttons === 1) this.updateChart(e.pageX - offset); });
        container.addEventListener('touchmove', (e) => { this.updateChart(e.changedTouches[0].pageX - offset); });
        window.addEventListener('resize', () => { this.updateChart(); setTimeout(() => this.updateChart(), 100); });

        this.updateChart();
    }

    private _datasets: Chart.ChartDataSets[] = [];
    get datasets(): Chart.ChartDataSets[] {
        const timeStep = (this.period * this.extendRange * YEAR) / GRAPH_POINTS;
        this.graphs.forEach((graph, index) => {
            if (!this._datasets) this._datasets = [];
            if (!this._datasets[index]) {
                const { r, g, b } = graph;
                this._datasets[index] = {
                    label: graph.label,
                    borderColor: rgba(r, g, b, 1),
                    backgroundColor: rgba(r, g, b, 0.1),
                    pointRadius: 0,
                    data: [],
                };
            }
            const points:ChartPoint[] = [];
            for (let i = 0; i < GRAPH_POINTS; ++i) {
                const x = this.start + i * timeStep;
                const y = graph.function(this.start, x);
                points.push({ x, y });
            }
            this._datasets[index].data = points;
        });

        return this._datasets;
    }

    private periodMaxY(period: number): number {
        const rangeEnd = this.start + period * YEAR;
        return Math.max(...this.graphs.map(graph => graph.function(this.start, rangeEnd)));
    }

    get currentMaxY(): number {
        return this.periodMaxY(this.period);
    }

    get maxRangeMaxY(): number {
        return this.periodMaxY(this.maxPeriod);
    }

    get minRangeMaxY(): number {
        return this.periodMaxY(this.minPeriod);
    }

    get calculateInitialPosition(): number {
        this.chart.update(); // make sure the new data is loaded
        const xAxis = this.chart.scales['x-axis-0'];
        const canvas = this.$refs.canvas as HTMLCanvasElement;
        const goal = this.start + (this.period * YEAR) + 1000 * 60 * 60 * 12; // mid of that day

        const STEPS = 10;
        let counter = 0;
        const search = (from: number, to: number): number => {
            const step = (to - from) / STEPS;
            let delta = Number.MAX_VALUE;
            for (let x = 0; x <= STEPS; x++) {
                counter += 1;
                if (counter > 100) return 0;
                const pos = from + x * step;
                const time = new Date(xAxis.getValueForPixel(pos)).getTime();
                const d = Math.abs(time - goal);
                if (d > delta) {
                    // optimum was previous position
                    const optimal = pos - step;
                    if (delta > 12 * 1000 * 60 * 60) { // more than 12h off, look closer
                        return search(optimal - step, optimal + step);
                    }
                    return optimal;
                }
                if (d < delta) {
                    delta = d;
                }
            }
            console.error('opimum not found');
            return 0;
        };

        const position = search(0, canvas.offsetWidth) || xAxis.right;
        return position;
    }
}

