
import '@nimiq/style/nimiq-style.min.css';
import '@nimiq/vue-components/dist/NimiqVueComponents.css';

import { Component, Vue } from 'vue-property-decorator';
import Welcome from './components/Welcome.vue';
import Calculator from './components/Calculator.vue';
import { isPrestakingEmbedLayout } from './lib/layout';

if (isPrestakingEmbedLayout) {
    document.querySelector('header.logo')!.classList.add('display-none');
}

@Component({ components: { Welcome, Calculator } })
export default class App extends Vue {
    private intro = !isPrestakingEmbedLayout;

    startCalculator() {
        this.intro = false;
        document.querySelector('header.logo')!.classList.remove('hidden');
    }
}
