import { posSupplyAt } from './pos';

// = 1 - POS_SUPPLY_DECAY ** (1000 * 60 * 60 * 24)
const DECAY_PER_DAY = 0.0003432600460362;

/**
 * Calculates the potential wealth accumulation based on staking in a cryptocurrency network,
 * considering the effects of reward decay over time. It computes the final amount of cryptocurrency
 * after a specified number of days of staking, taking into account whether the rewards are restaked or not.
 *
 * @param {number} initiallyStaked - The initial amount of cryptocurrency staked, in NIM.
 * @param {number} networkStakingRatio - The ratio of the total staked cryptocurrency to the total supply.
 * @param {number} daysStaked - The number of days the cryptocurrency is staked.
 * @param {number} genesisSupply - The PoS genesis supply of the cryptocurrency, in NIM.
 * @param {number} genesisTime - The millisecond timestamp of the PoS genesis block.
 * @param {boolean} restaking - Indicates whether the staking rewards are restaked (default is true).
 * Restaked mean that each staking reward is added to the pool of staked cryptocurrency for compound interest.
 * @returns {number} The total amount of cryptocurrency after staking for the specified number of days,
 * considering the decay of rewards and whether the rewards are restaked, in NIM.
 */
export default function wealth(
    initiallyStaked: number,
    networkStakingRatio: number,
    daysStaked: number,
    genesisSupply: number,
    genesisTime: number,
    restaking = true,
): number {
    const initialRewardsPerDay = posSupplyAt(genesisSupply, genesisTime, genesisTime + 24 * 60 * 60 * 1000) - genesisSupply;

    const decayFactor = Math.E ** (-DECAY_PER_DAY * daysStaked);
    const rewardFactor = initialRewardsPerDay / (DECAY_PER_DAY * networkStakingRatio * genesisSupply);

    if (restaking) {
        return initiallyStaked * (1 + rewardFactor * (1 - decayFactor));
    }

    return initiallyStaked * (
        1 + (1 / networkStakingRatio) * (
            Math.log(
                (DECAY_PER_DAY * genesisSupply * (1 / decayFactor))
                + initialRewardsPerDay * (1 / decayFactor) - initialRewardsPerDay,
            )
            - DECAY_PER_DAY * daysStaked
            - Math.log(DECAY_PER_DAY * genesisSupply)
        )
    );
}
