
import { Component, Vue } from 'vue-property-decorator';
import { ArrowRightSmallIcon } from '@nimiq/vue-components';

import Graph, { GraphEntry } from './Graph.vue';
import Settings, { Setting, defaultSettings } from './Settings.vue';
import NimiqButton from './NimiqButton.vue';
import wealth from '../lib/math';
import { powBlockHeightAt, powSupplyAfter } from '../lib/pow';
import { isPrestakingEmbedLayout } from '../lib/layout';

@Component({ components: { Graph, Settings, ArrowRightSmallIcon, NimiqButton } })
export default class Calculator extends Vue {
    private graphs: GraphEntry[] = [{
        label: 'Normal',
        enabled: !isPrestakingEmbedLayout,
        r: 233, // nimiq-gold
        g: 178,
        b: 19,
        function: (start: number, position: number) => this.calculate((position - start) / (24 * 60 * 60 * 1000), false),
    }, {
        label: 'restake',
        enabled: isPrestakingEmbedLayout,
        r: 33, // nimiq-green
        g: 188,
        b: 165,
        function: (start: number, position: number) => this.calculate((position - start) / (24 * 60 * 60 * 1000), true),
    }];

    private readonly maxPeriod = 10;
    private readonly minPeriod = 0.5;
    private settings = defaultSettings;
    private initialSupply = 0;
    private isPrestakingEmbedLayout = isPrestakingEmbedLayout;

    async update(settings: Setting) {
        const newSupply = powSupplyAfter(powBlockHeightAt(new Date(settings.fork)));
        const resetSlider = this.settings.period !== settings.period
            || this.initialSupply !== newSupply;
        this.settings = settings;
        this.graphs[1].enabled = settings.restake;
        this.initialSupply = newSupply;

        await this.$nextTick();
        (this.$refs.graph as Graph).update(resetSlider);
    }

    calculate(daysStaked: number, restaking = true): number {
        const { staking, ratio, fee, fork } = this.settings;
        const gain = wealth(
            staking,
            ratio,
            daysStaked,
            this.initialSupply,
            new Date(fork).getTime(),
            restaking,
        ) - staking;
        return gain * (1 - fee);
    }
}
