/**
 * Proof-of-Stake supply calculations
 */

import { powi, TOTAL_SUPPLY } from './utils';

// Supply decay per millisecond
const SUPPLY_DECAY = 0.9999999999960264;

// eslint-disable-next-line import/prefer-default-export
export function posSupplyAt(genesisSupply: number, genesisTime: number, currentTime: number): number {
    const t = currentTime - genesisTime;
    if (t < 0) {
        throw new Error('currentTime must be greater or equal to genesisTime');
    }

    return (TOTAL_SUPPLY - ((TOTAL_SUPPLY - genesisSupply * 1e5) * powi(SUPPLY_DECAY, t))) / 1e5; // Luna >> NIM
}
